"use client";

import { useParams, useRouter } from "next/navigation";
import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { ArrowLeft, Copy, RefreshCw, CheckCircle2, Loader2 } from "lucide-react";
import { useCharacter } from "@/context/CharacterContext";
import Link from "next/link";
import { APIKeyManager } from "@/components/api-key-manager";
import { validateAPIKey, generatePersonality, generateScenario, generateInitialMessage } from "@/lib/generation/service";
import { isAPIKeyConnected } from "@/lib/api-key";

type SectionId = "personality" | "scenario" | "initialMessage";

interface SectionState {
  loading: boolean;
  error: string | null;
  content: string;
}

export default function CharacterResultPage() {
  const params = useParams();
  const router = useRouter();
  const { characters, updateCharacter } = useCharacter();
  const [copied, setCopied] = useState<string | null>(null);
  const [showAPIKeyModal, setShowAPIKeyModal] = useState(false);
  const [scenarioInput, setScenarioInput] = useState<string>("");
  const [showScenarioModal, setShowScenarioModal] = useState(false);
  const [hasGenerated, setHasGenerated] = useState(false);

  const character = characters.find((char) => char.id === params.id);

  const [sections, setSections] = useState<Record<SectionId, SectionState>>({
    personality: {
      loading: false,
      error: null,
      content: character?.generatedContent?.personality || "",
    },
    scenario: {
      loading: false,
      error: null,
      content: character?.generatedContent?.scenario || "",
    },
    initialMessage: {
      loading: false,
      error: null,
      content: character?.generatedContent?.initialMessage || "",
    },
  });

  // Check API key and generate on mount
  useEffect(() => {
    if (!character) return;

    // Check if already generated
    if (character.generatedContent?.personality) {
      setSections({
        personality: {
          loading: false,
          error: null,
          content: character.generatedContent.personality || "",
        },
        scenario: {
          loading: false,
          error: null,
          content: character.generatedContent.scenario || "",
        },
        initialMessage: {
          loading: false,
          error: null,
          content: character.generatedContent.initialMessage || "",
        },
      });
      setHasGenerated(true);
      return;
    }

    // Check API key
    if (!isAPIKeyConnected()) {
      setShowAPIKeyModal(true);
      return;
    }

    // Start generation
    handleGenerateAll();
  }, [character?.id]);

  const handleGenerateAll = async () => {
    if (!character) return;

    const keyCheck = validateAPIKey();
    if (!keyCheck.valid || !keyCheck.apiKey || !keyCheck.provider) {
      setShowAPIKeyModal(true);
      return;
    }

    // Generate personality first
    await handleGenerateSection("personality", keyCheck.apiKey, keyCheck.provider);

    // Show scenario modal after personality
    setShowScenarioModal(true);
  };

  const handleGenerateSection = async (
    sectionId: SectionId,
    apiKey?: string,
    provider?: any
  ) => {
    if (!character) return;

    const keyCheck = apiKey && provider ? { valid: true, apiKey, provider } : validateAPIKey();
    if (!keyCheck.valid || !keyCheck.apiKey || !keyCheck.provider) {
      setShowAPIKeyModal(true);
      return;
    }

    setSections((prev) => ({
      ...prev,
      [sectionId]: { ...prev[sectionId], loading: true, error: null },
    }));

    try {
      let content = "";

      if (sectionId === "personality") {
        content = await generatePersonality(character, keyCheck.apiKey, keyCheck.provider);
      } else if (sectionId === "scenario") {
        const userScenario = scenarioInput.trim() || undefined;
        content = await generateScenario(character, userScenario, keyCheck.apiKey, keyCheck.provider);
      } else if (sectionId === "initialMessage") {
        const scenario = sections.scenario.content || undefined;
        content = await generateInitialMessage(character, scenario, keyCheck.apiKey, keyCheck.provider);
      }

      // Update section state
      setSections((prev) => ({
        ...prev,
        [sectionId]: { loading: false, error: null, content },
      }));

      // Update character context
      const charIndex = characters.findIndex((c) => c.id === character.id);
      if (charIndex !== -1) {
        const currentContent = character.generatedContent || {};
        updateCharacter(charIndex, {
          generatedContent: {
            ...currentContent,
            [sectionId === "personality" ? "personality" : sectionId === "scenario" ? "scenario" : "initialMessage"]: content,
          },
        });
      }
    } catch (error: any) {
      setSections((prev) => ({
        ...prev,
        [sectionId]: {
          loading: false,
          error: error.message || "Generation failed",
          content: prev[sectionId].content,
        },
      }));
    }
  };

  const handleScenarioSubmit = async () => {
    setShowScenarioModal(false);
    // Generate scenario
    await handleGenerateSection("scenario");
    // Then generate initial message
    setTimeout(async () => {
      await handleGenerateSection("initialMessage");
    }, 500);
  };

  const handleSkipScenario = async () => {
    setShowScenarioModal(false);
    setScenarioInput("");
    // Generate scenario without user input
    await handleGenerateSection("scenario");
    // Then generate initial message
    setTimeout(async () => {
      await handleGenerateSection("initialMessage");
    }, 500);
  };

  const handleRewrite = async (sectionId: SectionId) => {
    await handleGenerateSection(sectionId);
  };

  const copyToClipboard = (text: string, section: string) => {
    navigator.clipboard.writeText(text);
    setCopied(section);
    setTimeout(() => setCopied(null), 2000);
  };

  if (!character) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center px-4">
        <div className="text-center">
          <p className="text-xl font-semibold text-foreground mb-4">Character not found</p>
          <Link
            href="/create"
            className="inline-block px-6 py-3 rounded-xl bg-violet-500 hover:bg-violet-600 text-white font-medium transition-colors"
          >
            Create Character
          </Link>
        </div>
      </div>
    );
  }

  const displaySections = [
    {
      id: "personality" as SectionId,
      title: "Personality",
      content: sections.personality.content,
      loading: sections.personality.loading,
      error: sections.personality.error,
    },
    {
      id: "scenario" as SectionId,
      title: "Scenario",
      content: sections.scenario.content,
      loading: sections.scenario.loading,
      error: sections.scenario.error,
    },
    {
      id: "initialMessage" as SectionId,
      title: "Initial Message",
      content: sections.initialMessage.content,
      loading: sections.initialMessage.loading,
      error: sections.initialMessage.error,
    },
  ];

  return (
    <div className="min-h-screen bg-background px-4 py-8">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <Link
            href="/wizard"
            className="inline-flex items-center gap-2 text-muted-foreground hover:text-foreground transition-colors mb-6"
          >
            <ArrowLeft className="w-5 h-5" />
            <span className="text-sm">Back to Wizard</span>
          </Link>
          <h1 className="text-4xl font-bold text-foreground mb-2">{character.basics.name}</h1>
          <p className="text-muted-foreground">
            {character.basics.setting} ‚Ä¢ {character.basics.age} years old
          </p>
        </div>

        {/* Character Sections */}
        <div className="space-y-6">
          {displaySections.map((section, index) => (
            <motion.div
              key={section.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
              className="glass rounded-2xl p-6 border border-border"
            >
              <div className="flex items-center justify-between mb-4">
                <h2 className="text-xl font-semibold text-foreground">{section.title}</h2>
                <div className="flex items-center gap-2">
                  <button
                    onClick={() => copyToClipboard(section.content || "", section.id)}
                    disabled={!section.content || section.loading}
                    className="px-3 py-1.5 rounded-lg glass border border-border hover:bg-white/5 transition-colors flex items-center gap-2 text-sm disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    {copied === section.id ? (
                      <>
                        <CheckCircle2 className="w-4 h-4 text-green-400" />
                        Copied!
                      </>
                    ) : (
                      <>
                        <Copy className="w-4 h-4" />
                        Copy
                      </>
                    )}
                  </button>
                  <button
                    onClick={() => handleRewrite(section.id)}
                    disabled={section.loading}
                    className="px-3 py-1.5 rounded-lg glass border border-border hover:bg-white/5 transition-colors flex items-center gap-2 text-sm disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    {section.loading ? (
                      <Loader2 className="w-4 h-4 animate-spin" />
                    ) : (
                      <RefreshCw className="w-4 h-4" />
                    )}
                    Rewrite
                  </button>
                </div>
              </div>

              {section.loading ? (
                <div className="flex items-center justify-center py-12">
                  <Loader2 className="w-6 h-6 animate-spin text-violet-400" />
                  <span className="ml-3 text-muted-foreground">Generating...</span>
                </div>
              ) : section.error ? (
                <div className="py-4 px-4 rounded-xl bg-red-500/10 border border-red-500/30">
                  <p className="text-sm text-red-400">{section.error}</p>
                  <button
                    onClick={() => handleRewrite(section.id)}
                    className="mt-2 text-xs text-red-400 hover:text-red-300 underline"
                  >
                    Try again
                  </button>
                </div>
              ) : (
                <textarea
                  value={section.content}
                  onChange={(e) => {
                    const charIndex = characters.findIndex((c) => c.id === character.id);
                    if (charIndex !== -1) {
                      const currentContent = character.generatedContent || {};
                      updateCharacter(charIndex, {
                        generatedContent: {
                          ...currentContent,
                          [section.id === "personality" ? "personality" : section.id === "scenario" ? "scenario" : "initialMessage"]: e.target.value,
                        },
                      });
                    }
                    setSections((prev) => ({
                      ...prev,
                      [section.id]: { ...prev[section.id], content: e.target.value },
                    }));
                  }}
                  className="w-full min-h-32 px-4 py-3 rounded-xl glass border border-border focus:border-violet-500 transition-all outline-none resize-y text-sm font-mono"
                  placeholder={section.content ? "" : "Content will be generated here..."}
                />
              )}
            </motion.div>
          ))}
        </div>

        {/* Actions */}
        <div className="mt-8 flex gap-4">
          <Link
            href="/create"
            className="flex-1 py-4 rounded-2xl glass border border-border hover:bg-white/5 transition-colors text-center font-medium"
          >
            Create Another
          </Link>
          <button
            onClick={() => router.push("/wizard")}
            className="flex-1 py-4 rounded-2xl bg-violet-500 hover:bg-violet-600 text-white font-semibold transition-colors"
          >
            Edit Character
          </button>
        </div>
      </div>

      {/* API Key Manager */}
      <APIKeyManager
        isOpen={showAPIKeyModal}
        onClose={() => setShowAPIKeyModal(false)}
        onSave={() => {
          setShowAPIKeyModal(false);
          if (!hasGenerated) {
            handleGenerateAll();
          }
        }}
      />

      {/* Scenario Modal */}
      {showScenarioModal && (
        <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
          <div
            className="fixed inset-0 bg-black/50 backdrop-blur-sm"
            onClick={() => setShowScenarioModal(false)}
          />
          <motion.div
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            className="glass rounded-3xl p-8 max-w-2xl w-full border border-border relative z-10"
          >
            <h2 className="text-2xl font-bold text-foreground mb-4">Define Scenario (Optional)</h2>
            <p className="text-muted-foreground mb-6">
              You can provide a custom scenario or let AI generate one based on your character.
            </p>
            <textarea
              value={scenarioInput}
              onChange={(e) => setScenarioInput(e.target.value)}
              placeholder="e.g., The character is in a coffee shop, waiting for someone. It's raining outside..."
              className="w-full h-32 px-4 py-3 rounded-xl glass border border-border focus:border-violet-500 transition-all outline-none resize-none mb-6"
            />
            <div className="flex gap-3">
              <button
                onClick={handleSkipScenario}
                className="flex-1 py-3 rounded-xl glass border border-border hover:bg-white/5 transition-colors font-medium"
              >
                Skip (AI Generate)
              </button>
              <button
                onClick={handleScenarioSubmit}
                className="flex-1 py-3 rounded-xl bg-violet-500 hover:bg-violet-600 text-white font-medium transition-colors"
              >
                Continue
              </button>
            </div>
          </motion.div>
        </div>
      )}
    </div>
  );
}
