/**
 * AI Generation Service
 * Handles all AI content generation with API key management
 */

import { CharacterState } from "@/context/CharacterContext";
import { getAPIKey, getSelectedProvider, APIProvider } from "@/lib/api-key";
import { OpenAIProvider, GeminiProvider } from "@/lib/ai/providers";
import {
  buildJanitorPersonalityPrompt,
  getPersonalitySystemPrompt,
  buildScenarioPrompt,
  buildInitialMessagePrompt,
} from "@/lib/prompts/janitor-ai";

export interface GenerationState {
  loading: boolean;
  error: string | null;
  content: string | null;
}

export interface CharacterGenerationState {
  personality: GenerationState;
  scenario: GenerationState;
  initialMessage: GenerationState;
}

/**
 * Get AI provider instance
 */
function getProvider(provider: APIProvider): OpenAIProvider | GeminiProvider {
  if (provider === "openai" || provider === "openrouter") {
    return new OpenAIProvider();
  }
  return new GeminiProvider();
}

/**
 * Generate personality content in Janitor AI format
 */
export async function generatePersonality(
  character: CharacterState,
  apiKey: string,
  provider: APIProvider
): Promise<string> {
  const systemPrompt = getPersonalitySystemPrompt();
  const userPrompt = buildJanitorPersonalityPrompt(character);
  
  const aiProvider = getProvider(provider);
  
  // For OpenAI, use system/user messages
  if (provider === "openai" || provider === "openrouter") {
    const fullPrompt = `${systemPrompt}\n\n${userPrompt}`;
    return await aiProvider.generate(fullPrompt, apiKey, provider === "openrouter" ? "openai/gpt-4" : "gpt-4");
  }
  
  // For Gemini, combine into single prompt
  const fullPrompt = `${systemPrompt}\n\n${userPrompt}`;
  return await aiProvider.generate(fullPrompt, apiKey, "gemini-1.5-flash");
}

/**
 * Generate scenario
 */
export async function generateScenario(
  character: CharacterState,
  userScenario: string | undefined,
  apiKey: string,
  provider: APIProvider
): Promise<string> {
  const prompt = buildScenarioPrompt(character, userScenario);
  const aiProvider = getProvider(provider);
  
  if (provider === "openai" || provider === "openrouter") {
    return await aiProvider.generate(prompt, apiKey, provider === "openrouter" ? "openai/gpt-4" : "gpt-4");
  }
  
  return await aiProvider.generate(prompt, apiKey, "gemini-1.5-flash");
}

/**
 * Generate initial message
 */
export async function generateInitialMessage(
  character: CharacterState,
  scenario: string | undefined,
  apiKey: string,
  provider: APIProvider
): Promise<string> {
  const prompt = buildInitialMessagePrompt(character, scenario);
  const aiProvider = getProvider(provider);
  
  if (provider === "openai" || provider === "openrouter") {
    return await aiProvider.generate(prompt, apiKey, provider === "openrouter" ? "openai/gpt-4" : "gpt-4");
  }
  
  return await aiProvider.generate(prompt, apiKey, "gemini-1.5-flash");
}

/**
 * Validate API key exists
 */
export function validateAPIKey(): { valid: boolean; apiKey: string | null; provider: APIProvider | null } {
  const provider = getSelectedProvider();
  if (!provider) {
    return { valid: false, apiKey: null, provider: null };
  }
  
  const apiKey = getAPIKey(provider);
  if (!apiKey || apiKey.trim() === "") {
    return { valid: false, apiKey: null, provider: null };
  }
  
  return { valid: true, apiKey, provider };
}
